import { useTranslation } from "gatsby-plugin-react-i18next";

import { Edition } from "../../data";
import { mainSponsor, sponsors, partners } from "../general/sponsors";

import * as styles from "./partners.module.css";

type SponsorKind = "main" | "regular";

type SponsorProps = {
  kind?: SponsorKind;
  link: string;
  image: any;
  name: string;
};

const Sponsor: React.FC<SponsorProps> = ({ kind, link, image, name }) => {
  return (
    <a
      className={styles.sponsor}
      target={"_blank"}
      rel={"noreferrer"}
      href={link}
    >
      <img
        src={image}
        alt={name}
        className={kind === "main" ? styles.mainSponsor : styles.sponsorImage}
      />
    </a>
  );
};

Sponsor.defaultProps = {
  kind: "regular",
};

type PartnersProps = {
  edition: Edition;
};

const Partners: React.FC<PartnersProps> = ({ edition }) => {
  const { t } = useTranslation();

  const editionMainSponsor = mainSponsor[edition];

  return (
    <div className={`page ${styles.partners}`}>
      {editionMainSponsor && (
        <h1 className={`title ${styles.partnersTitle}`}>{t("main_sponsor")}</h1>
      )}

      {editionMainSponsor && (
        <Sponsor
          kind="main"
          name={editionMainSponsor.name}
          link={editionMainSponsor.link}
          image={editionMainSponsor.image}
        />
      )}

      <h2 className={`title ${styles.partnersTitle}`}>{t("subtitle1")}</h2>
      <div className={styles.sponsorList}>
        {sponsors[edition].map((item, index) => (
          <Sponsor
            key={"sponsor2022" + index}
            name={item.name}
            link={item.link}
            image={item.image}
          />
        ))}
      </div>

      <h2 className={`title ${styles.partnersTitle}`}>{t("subtitle2")}</h2>
      <div className={styles.partnerList}>
        {partners[edition].map((item, index) => (
          <Sponsor
            key={"partner2022" + index}
            name={item.name}
            link={item.link}
            image={item.image}
          />
        ))}
      </div>

      <p>{t("partnersDetails")}</p>
      <div className={`button ${styles.button}`}>
        <a
          className={"buttonLink"}
          target={"_blank"}
          rel={"noreferrer"}
          href={"mailto:contact@nitronlp.rocks"}
        >
          {t("partnersButton")}
        </a>
      </div>
    </div>
  );
};

export default Partners;
